import React from 'react';

import '../assets/styles/CardContainer.scss'

const Card = (props) => {

    const { image, source, title, description} = props;

    return(
        <div className="tarjeta">
            <div className="tarjeta__img" >
                <img src={image} alt={title} />
            </div>
            <div className="detalles" >
                <h3>{title}</h3>
                <p>{description}</p>
                <a href={source} >Ir al sitio</a>
            </div>
        </div>
    )
};


export default Card;