import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../assets/styles/Navbar.scss';

class Navbar extends Component {

    state = {
        clicked: false
    }

    handleClick = () => {
        this.setState({ clicked: !this.state.clicked })
    }

    render() {
        return(
            <nav className="navbarItems" >
                <Link to="/" >
                    <h1 className="navbar__logo" > Alejandro Schwartz </h1>
                </Link>
                <div className="menu__icon" onClick={this.handleClick} >
                    <i className={this.state.clicked ? 'fas fa-times' : 'fas fa-bars'} ></i>
                </div>
                <ul className={this.state.clicked ? 'nav__menu active' : 'nav__menu'} >
                    <Link to="/" className="nav__links" >
                        <li>Inicio</li>
                    </Link>
                    <Link to="/proyect" className="nav__links" >
                        <li>Proyectos</li>
                    </Link>
                    <Link to="/contact" className="nav__links" >
                        <li>Contacto</li>
                    </Link>
                </ul>
            </nav>
        )
    }
}

export default Navbar;