import React from 'react';

import '../assets/styles/CardContainer.scss';

const CardContainer = ({children}) => (
     
    <div className="tarjetas" >

        <div className="wave3" >
            <svg className="wave__svg3" viewBox="0 0 500 150" preserveAspectRatio="none" >
                <path className="wave__path3"
                    d="M0.00,49.98 C300.51,153.45 210.21,153.45 500.00,49.98 L500.00,0.00 L0.00,0.00 Z" >
                </path>
            </svg>
        </div>

        <h2 className="tarjetas__h2" id="Proyectos" >
            MIS PROYECTOS
        </h2>

        {children}

        <div className="wave" >
            <svg className="wave__svg" viewBox="0 0 500 150" preserveAspectRatio="none" >
                <path className="wave__path"
                    d="M-1.41,82.39 C171.27,149.50 328.72,-2.45 502.54,89.30 L500.00,150.00 L0.00,150.00 Z" >
                </path>
            </svg>
        </div>

    </div>
)

export default CardContainer;